export default {
	created() {
		var neirong = this.questionList[this.number-1]
		if(this.tag == "l"){
			neirong = this.questionList[this.qNumber]
		}
		this.content = neirong.content
		this.jiexi = neirong.jiexi
	},
	data() {
		return {
			content: '',
			jiexi: ''
		};
	},
	props:[
		"number",
		"questionList",
		"nextText",
		"jiexiTag",
		"qNumber",
		"tag"
	]
}
